$clock-total-height: 69px;
$clock-image: '../img/sprite.png';
$clock: (52px, 25px, -52px, -25px, 26px, 26px, 80px, 69px, '../img/sprite.png', 'clock', );
$exchange-name: 'exchange';
$exchange-x: 52px;
$exchange-y: 0px;
$exchange-offset-x: -52px;
$exchange-offset-y: 0px;
$exchange-width: 28px;
