$exchange-total-width: 80px;
$exchange-total-height: 69px;
$exchange-image: '../img/sprite.png';
$exchange: (52px, 0px, -52px, 0px, 28px, 25px, 80px, 69px, '../img/sprite.png', 'exchange', );
$group-name: 'group';
$group-x: 0px;
$group-y: 25px;
$group-offset-x: 0px;
$group-offset-y: -25px;
$group-width: 30px;
$group-height: 26px;
$group-total-width: 80px;
$group-total-height: 69px;
$group-image: '../img/sprite.png';
$group: (0px, 25px, 0px, -25px, 30px, 26px, 80px, 69px, '../img/sprite.png', 'group', );
$phone-name: 'phone';
$phone-x: 0px;
$phone-y: 51px;
