body {
  font-family: 'HelveticaNeueCyr';
  font-weight: 300;
  font-variant: normal;
}

.page {
  width: 100%;
}

.wrapper {
  width: 100%;
  height: auto;
  margin: 0 auto;
  background-color: #ccc;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
}

.frame {
  width: 86.74963396778917%;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
}

@media screen and (min-width: 1367px) {
  .frame {
    width: 1366px;
  }
}

.header {
  background: #f5f5f5;
  height: auto;
}

.header_flex {
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
}

.block_call_flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 29.19831223628692%;
}

.block_call_element {
  display: flex;
  flex-direction: column;
  width: auto;
}

.block_call_element span {
  font-size: 0.75em;
  color: #454545;
  margin: 13px 0 11px 0;
}

.block_call_element a {
  font-size: 0.75em;
  color: #de271f;
  margin: 0 0 14px 0;
}

.block_info_list {
  margin: 26px 0 0 0;
}

.block_info_element {
  margin: 0 11px;
}

.block_info_element a {
  font-size: 0.75em;
  text-decoration: none;
  color: #003399;
}

.block_info_element a:hover {
  text-decoration: underline;
}

.block_header_button {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header_button {
  display: block;
  padding-top: 25px;
  padding-bottom: 25px;
  width: auto;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  font-size: 0.75em;
  transition: 0.5s;
  position: relative;
}

.header_button_phone {
  background: #de271f;
  padding-left: 49px;
  padding-right: 20px;
}

.header_button_phone:hover {
  background: rgba(255, 34, 25, 0.89);
}

.header_button_phone::before {
  content: '';
  position: absolute;
  left: 21px;
  top: 23px;
  background-image: url(../img/sprite.png);
  background-position: 0px -51px;
  width: 18px;
  height: 18px;
}

.header_button_basket {
  background: #003399;
  padding-left: 66px;
  padding-right: 34px;
}

.header_button_basket:hover {
  background: rgba(0, 48, 157, 0.7);
}

.header_button_basket::before {
  content: '';
  position: absolute;
  left: 37px;
  top: 23px;
  background-image: url(../img/sprite.png);
  background-position: -30px -25px;
  width: 20px;
  height: 17px;
}

@media (max-width: 567px) and (min-width: 0px) {
  .header_flex {
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
  }
  .block_call_flex {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }
  .block_info_list {
    margin: 15px 0 15px 0;
    width: 100%;
    text-align: center;
  }
  .block_info_element:first-child {
    margin: 0 0 0 0;
  }
  .block_info_element:last-child {
    margin: 0 0 0 0;
  }
  .block_header_button {
    width: 100%;
    justify-content: center;
  }
  .header_button {
    display: block;
    width: 164px;
    padding-left: 0;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .header_button_phone::before {
    left: 3px;
    top: 7px;
  }
  .header_button_basket::before {
    left: 8px;
    top: 7px;
  }
}

@media (max-width: 800px) and (min-width: 567px) {
  .header_flex {
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
  }
  .block_call_flex {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }
  .block_info_list {
    margin: 15px 0 15px 0;
    width: 100%;
    text-align: center;
  }
  .block_info_element:first-child {
    margin: 0 0 0 0;
  }
  .block_info_element:last-child {
    margin: 0 0 0 0;
  }
  .block_header_button {
    width: 100%;
    justify-content: center;
  }
  .header_button {
    display: block;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .header_button_phone::before {
    left: 21px;
    top: 7px;
  }
  .header_button_basket::before {
    left: 37px;
    top: 7px;
  }
}
